(function() {
  'use strict';

  angular
    .module('horseFrontend')
    .controller('AppController', AppController);

  /** @ngInject */
  function AppController($scope, toastr,$http,chart,$translate) {
  
var url='http://horseback-dev.ap-southeast-1.elasticbeanstalk.com/';
//var url='http://localhost/';

 
     //Get Race info
    $http({
  method: 'GET',
  url: url+'fetchRace/'
})
        .then(
        function successCallback(response) {
       $scope.raceLoadHidden=true;
    $scope.races=response.data;
        if($scope.races.length==0)
            $scope.raceNoData=true;
        else
            {
              
                $scope.raceSelect=true; 
            }
        
  }, 
        function errorCallback(response) {
    toastr.error('Error','Cannot connect to the server,please try it later')
  }
        );
      
 
       
      
      
      
  
      
      $scope.buttonText ='APP_SELECT_RACE';
      $scope.calc=function(){
    
    if(!$scope.selectedlink){toastr.error('Please Select a race first');return;}
     $scope.buttonText='APP_ANALYZING';
      $scope.isResultReady= true;
    
       $http({
  method: 'POST',
  url: url,
 data:{link:$scope.selectedlink,lang:$translate.use()}
}).then(function successCallback(response) {
        var horseList = response.data.hl; 
        $scope.raceinfo=response.data.ri;
           $scope.horseList =horseList;
            console.log(horseList)


           if(horseList.length==0)
           {toastr.error('Failed to get any data!')
           $scope.buttonText ='APP_NO_DATA';
            $scope.isResultReady= false;
           return;
           }
           
            $scope.buttonText ='APP_SELECT_RACE';
                  
           
           var horse_name =[];
           for(var i=0;i<horseList.length;i++){
              horse_name.push(horseList[i].horse);
           }
           var horse_no =[];
           for(var i=0;i<horseList.length;i++){
              horse_no.push(horseList[i].no);
           }
           
         var horse_grade =[];
           for(var i=0;i<horseList.length;i++){
              horse_grade.push(horseList[i].grade);
           }   
           
           chart.drawBar(horseList);
           chart.drawTrainer(horseList);
             chart.drawJockey(horseList);
           chart.drawHorseWeight(horseList);
          chart.drawWeight(horseList);
            chart.drawTop(horseList);
            chart.drawDraw(horseList);
            //chart.drawVeterinary(horseList);
          
          // $scope.$apply();

           
           
           
           
           
  }, function errorCallback(response) {
           $scope.buttonText ='Get Score';
    toastr.error('asdasd','asas')
  });
      
    
    
    
    
}

    
  }
})();

(function() {
  'use strict';

  angular
    .module('horseFrontend')
    .controller('LoginController', LoginController);

  /** @ngInject */
  function LoginController($rootScope,$scope, UserService, $location,toastr,SocketService) {
  
   
      
      $scope.UserLogin = function() {
 	
         
       
        $rootScope.buttonDisabled =   1
       SocketService.emit("login",{ username: $scope.username, password: $scope.password })

                   
                             
        
       
    
    
    
    
    
    
    }
      
        
        

    
  }
})();
(function() {
  'use strict';

  angular
    .module('horseFrontend')
    .directive('racenav', acmeNavbar);

  /** @ngInject */
  function acmeNavbar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/view/navbar.html',

      controller: NavbarController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
   function NavbarController($http,$rootScope, UserService, $location,toastr,SocketService,$translate) {




      function getWeather(){


                //Get Race info
    $http({method: 'GET',url: 'http://api.openweathermap.org/data/2.5/find?q=HONGKONG&APPID=595ebb307096a30a2521d0436ebb976f&units=metric '
}).then(
        function successCallback(response) {

    $rootScope.temp=response.data.list[0].main.temp;
     $rootScope.humi=response.data.list[0].main.humidity;
     $rootScope.wind=response.data.list[0].wind.speed;
          console.log(response.data.list[0])
  },
        function errorCallback(response) {
   // toastr.error('Error','Cannot connect to the server,please try it later')
  }
        );



      }

        getWeather();

         SocketService.logoutfailed(function(res){

                toastr.error(res)
            })
        SocketService.logoutok(function(res){

                    UserService.logout()
                   $location.path('/login')
                    toastr.success("Good Bye! ",{
  allowHtml: true
})
            })

        SocketService.loginfailed(function(res){
            console.log('login failed')
            toastr.error(res.msg)
                $rootScope.buttonDisabled =    0

            })

        SocketService.loginok(function(res){
                        console.log('login ok')
                   $rootScope.buttonDisabled =    0
                    UserService.login(res)
                    $location.path('/app')
                      toastr.success("Welcome Back ! "+res.username)
            })





        $rootScope.UserLogout = function() {

         SocketService.emit('logout',SocketService.id)



 	}


$rootScope.currentLang=function()
{
    return $translate.use();

}
  $rootScope.changeLanguage = function (key) {

    $translate.use(key);
  };


$rootScope.mobileClose=true;
  
       $rootScope.mobileTogle=function(){
           
           $rootScope.mobileClose=!$rootScope.mobileClose;
       }

  }





  }

})();

