// teaser
$(window).load(function() {
    "use strict";
    var tid = setInterval(animateTeaser, 4000);
    var animCount = 0;

    function animateTeaser() {
        animCount++;
        if (animCount > 3) {
            animCount = 0;
            $('.teaser-text-animation.active').fadeTo(300, 0, function() {
                $('.teaser-text-animation').removeClass('active');
                $('.teaser-text-animation').removeClass('first');
                $('.teaser-normal').css({
                    marginTop: '100px'
                });
                $('.teaser-highlight').css({
                    marginTop: '-100px'
                });
                $('.teaser-text-animation:first').addClass('active').fadeTo(300, 1, function() {
                    $('.teaser-normal, .teaser-highlight, .teaser-text-animation:first').each(function(wordCount) {
                        $(this).animate({
                            marginTop: 0
                        }, {
                            duration: 400,
                            queue: false
                        });
                    });
                });
            });
        } else {
            var nextAnim = $('.teaser-text-animation').get(animCount);
            $('.teaser-text-animation.active').fadeTo(300, 0, function() {
                $('.teaser-text-animation').removeClass('active');
                $('.teaser-normal').css({
                    marginTop: '100px'
                });
                $('.teaser-highlight').css({
                    marginTop: '-100px'
                });
                $(nextAnim).addClass('active').fadeTo(300, 1, function() {
                    $('.teaser-normal, .teaser-highlight', nextAnim).each(function(wordCount) {
                        $(this).animate({
                            marginTop: 0
                        }, {
                            duration: 400,
                            queue: false
                        });
                    });
                });
            });
        }
    }
});


// mobile-detect
var isMobile = {
    Android: function() {
        return navigator.userAgent.match(/Android/i);
    },
    BlackBerry: function() {
        return navigator.userAgent.match(/BlackBerry/i);
    },
    iOS: function() {
        return navigator.userAgent.match(/iPhone|iPad|iPod/i);
    },
    Opera: function() {
        return navigator.userAgent.match(/Opera Mini/i);
    },
    Windows: function() {
        return navigator.userAgent.match(/IEMobile/i);
    },
    any: function() {
        return (isMobile.Android() || isMobile.BlackBerry() || isMobile.iOS() || isMobile.Opera() || isMobile.Windows());
    }
};
// JavaScript Document

// newsletter form
$(document).ready(function() {
	$('form#subscribe').submit(function() {
	$('form#subscribe .subscribeerror').remove();
	var hasError = false;
	$('.subscriberequiredField').each(function() {
	if(jQuery.trim($(this).val()) == '') {
    var labelText = $(this).prev('label').text();
    $(this).parent().append('<span class="subscribeerror">Please enter your'+labelText+'</span>');
    $(this).addClass('inputError');
    hasError = true;
    } else if($(this).hasClass('subscribeemail')) {
    var subscribeemailReg = /^([\w-\.]+@([\w-]+\.)+[\w-]{2,4})?$/;
    if(!subscribeemailReg.test(jQuery.trim($(this).val()))) {
    var labelText = $(this).prev('label').text();
    $(this).parent().append('<span class="subscribeerror">Please enter a valid'+labelText+'</span>');
    $(this).addClass('inputError');
    hasError = true;
    }
    }
    });
    if(!hasError) {
    $('form#subscribe input.submit').fadeOut('normal', function() {
    $(this).parent().append('');
    });
    var formInput = $(this).serialize();
    $.post($(this).attr('action'),formInput, function(data){
    $('form#subscribe').slideUp("fast", function() {
    $(this).before('<div class="subscribesuccess">Thank you for subscribing.</div>');
    });
    });
    }
    return false;
    });
});
